body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;  
  font-size: calc(10px + 2vmin);
  color: black;
  padding: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {
	display:none !important;
}

.spell-buttons {
	margin: 5px;
	font-size:14px;
}

.blue {
	color:#30b0db;
	font-weight:bold;
	font-size: 18px;
}

.center-button {
	margin: auto;
	display:block;
}

.my-title {
	text-align:center;
}

.choice-boxes {
	text-align:right;
}

input[type=number]::-webkit-inner-spin-button {
    opacity: 1
}

.center {
	text-align:center;
	margin-left: auto;
	margin-right: auto;
	margin-top:10px;
	margin-bottom:5px;
	font-size: 14px;
}

.choose {
	margin-top:15px;
	font-weight:bold;
	font-size:18px;
}

.spell-slot {
	border: 1px black solid;
	margin-right: 5px;
	margin-left: 5px;
	border-radius: 5px;
	box-shadow: 1px 4px gray;
	margin-bottom:10px;
}

.spell-slot:hover {
	cursor:pointer;
}

.spell-slot:active {
  box-shadow: 0px 2px gray;
  margin-right: 4px;
  margin-left: 6px;
  margin-top: 2px;
  margin-bottom:8px;
}

.no-select {
	-webkit-user-select: none; /* Chrome/Safari */        
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* IE10+ */
}
.App {
  text-align: center;
  padding:10px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;  
  justify-content:center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

